<template>
  <Alert
    v-model="serverError"
  />

  <div
    class="flex flex-col items-center justify-center
      bg-base-bg-light dark:bg-base-bg min-h-100vh"
  >
    <div class="w-84">
      <div class="text-2xl font-medium text-fg-light dark:text-fg mb-2 text-center">
        Регистрация аккаунта
      </div>

      <div class="text-sm font-light text-fg-light dark:text-fg mb-12 text-center">
        Для продолжения зарегистрируйтесь
      </div>

      <UiInput
        v-model="email"
        placeholder="Введите логин..."
        class="text-sm h-12 w-full font-medium"/>

      <div class="text-red-400 text-sm mt-3">
        {{ emailError }}
      </div>

      <UiInput
        v-model="password"
        @input="validatePassword"
        placeholder="Введите пароль..."
        type="password"
        class="text-sm h-12 w-full font-medium mt-3"/>

      <div class="text-red-400 text-sm mt-3">
        {{ passwordError }}
      </div>

      <UiInput
        v-model="passwordRepeat"
        @input="validatePasswordRepeat"
        placeholder="Повторите пароль..."
        type="password"
        class="text-sm h-12 w-full font-medium mt-3"/>

      <div class="text-red-400 text-sm mt-3">
        {{ passwordRepeatError }}
      </div>

      <UiButton
        @click="login"
        class="text-sm h-12 w-full font-medium mt-6"
      >
        Зарегистрироваться
      </UiButton>

      <router-link
        to="/login"
        class="text-sm text-center mt-6 block underline text-fg-light dark:text-fg"
      >
        У меня уже есть аккаунт
      </router-link>
    </div>
  </div>
</template>

<script>
import UiButton from '@/components/ui/Button.vue';
import UiInput from '@/components/ui/Input.vue';

import Alert from '@/components/Alert.vue';

export default {
  name: 'Register',
  components: {
    UiButton,
    UiInput,

    Alert,
  },
  data() {
    return {
      email: '',
      emailError: '',
      password: '',
      passwordError: '',
      passwordRepeat: '',
      passwordRepeatError: '',

      serverError: '',
    };
  },
  methods: {
    login() {
      this.$store.dispatch('auth/register', {
        login: this.email,
        password: this.password,
      })
        .then(() => {
          this.$router.push(this.$route.params.nextUrl || '/');
        })
        .catch((err) => {
          this.serverError = err.message;
        });
    },
    validatePassword(event) {
      const password = event.target.value;

      if (password.length < 8) {
        this.passwordError = 'Длина пароля не может быть меньше 8 символов';
      } else {
        this.passwordError = '';
      }
    },
    validatePasswordRepeat(event) {
      const password = event.target.value;

      if (password !== this.password) {
        this.passwordRepeatError = 'Пароли не совпадают';
      } else {
        this.passwordRepeatError = '';
      }
    },
  },
};
</script>
